<template>
  <!-- Faqs Popup -->

<div class="modal fade" id="faqs-popup" tabindex="-1" role="dialog" aria-labelledby="faqs-popup" aria-hidden="true">
	<div class="modal-dialog window-popup faqs-popup" role="document">
		<div class="modal-content">
			<a href="#" class="close icon-close" data-dismiss="modal" aria-label="Close">
				<svg class="olymp-close-icon">
					<use xlink:href="svg-icons/sprites/icons.svg#olymp-close-icon"></use>
				</svg>
			</a>

			<div class="modal-body">
				<div class="whereto">
					<form class="col-12" role="form">
						<div class="row">
							<div class="col col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
								<response-message :data="errorData" />
								<div class="form-group label-floating is-empty">
									<label class="control-label">What Match ?</label>
									<input class="form-control col-12" type="text" id="input" @keyup="searchMatch" autocomplete="off" v-model="search"  placeholder="" style="border-top: none;border-left: none;border-right: none;">
								</div>
							</div>
						</div>
					</form>
					<div class="hide" id="tl" style="width:100%;text-align:center">
						<img style="margin" src="/image/lg.gooey-ball-lodaer.gif" width="60" />
					</div>
					<div ref="whereto" class="">
						<div v-for="(fixture, index) in fixtures" :key="index" class="post__author author vcard inline-items whatmatch">
							<router-link :to="{ name: 'Match', params: {fixture_id: getFid(fixture.id)} }">
								<div @click="closeModal" class="author-date" style="width:100%">
									<div class="post__date">
										<time class="published" datetime="2017-03-24T18:18">
											{{ dateString(fixture.event_date) }}
										</time>
									</div>
									<div :style="bgImage()" class="fix">
										<img :src="fixture.home_team_logo" alt="author">
										<span>{{fixture.home_team_name}}</span>
									</div>
									<span class="vs">VS</span>
									<div class="fix">
										<img :src="fixture.away_team_logo" alt="author">
										<span>{{fixture.away_team_name}}</span>
									</div>
									
								</div>
							</router-link>

						</div>
					</div>
					<button id="moreR" v-if="moreResult === true" @click="showMore()" class="btn btn-block bg-mdd">More</button>

				</div>
			</div>
		</div>
	</div>
</div>

<!-- ... end Faqs Popup -->
</template>

<script>
import jquery from "jquery";
// let $ = jquery;
var loader;

var pageData = {
	errorData: {},
	fixtures: {},
	search: "",
	moreResult: false,
	fid: 'ddd',
	storage: window.localStorage,
	// for search result pagination
	firstPageUrl: '',
	lastPageUrl: '',
	nextPageUrl: '',
};

export default {
	name: "Whereto",
	data() {
		return pageData;
	},
    methods: {
		bgImage() {
			return `background-image:url(${this.$store.state.staticImage})`;
		},
        showLoading() {
			let loader = this.$loader(this, this.$refs.whereto, {loaderType: "spinner"});
			
			return loader;
		},
		dateString(date) {
			return this.$getDateYear(date)
		},
		getFid(id) {
			return id;
		},
		closeModal() {
			$('#faqs-popup').modal('hide');
		},
		showMore() {
			var url = this.nextPageUrl;
			this.showWhereto(url);
		},
        showWhereto(nurl = null) {
			var fetchUrl;
			if(nurl !== null) {
				fetchUrl = nurl;
			}else {
				fetchUrl = "fixtures/all/5?page=1";
			}
			loader = this.showLoading();
			$("#moreR").removeClass('hide');
				this.axios.get(fetchUrl)
					.then(response => {
						loader.hide();
						// console.log(response.data.data);
						// let content;
						let fixtures = response.data.data.fixtures.data;
						// check if the result is more than the returned data
						this.moreResult = response.data.data.more;
						// bind fixtures to the data
						this.fixtures = fixtures;
						// pagination path
						this.firstPageUrl = response.data.data.fixtures.first_page_url;
						this.lastPageUrl = response.data.data.fixtures.last_page_url;
						this.nextPageUrl = response.data.data.fixtures.next_page_url;
					})
					.catch(error => {
						loader.hide();
						this.errorData = {
							message: error.response
							? error.response.data.message + " Sorry we had a problem getting the data requested for"
							: "Sorry an error occured, check your internet",
							isError: true
						};
					})
		},
		searchMatch() {
			// Let's warn the parent that a change was made
			let query = this.search;
			$("#tl").removeClass("hide");
			this.axios.get("fixtures/search/" + query)
					.then(response => {
						$("#tl").addClass("hide");
						let fixtures = response.data.data.fixtures;
						// console.log(response.data.data.fixtures);
						this.fixtures = fixtures;
						$("#moreR").addClass('hide');
					})
					.catch(error => {
						$("#tl").addClass("hide");
						this.errorData = {
							message: error.response
									? error.response.data.message + " Sorry we had a problem getting the data requested for"
									: "Sorry an error occured, check your internet",
							isError: true
						};
					})
		}
    },
    props: {
        whereto: Boolean
    },
    mounted() {
        // here we check if the whereto modal is hidden, if it is set the whereto props to false
        // so that once it's is clicked again it would load the api from the server
        $('#faqs-popup').on('hidden.bs.modal', function () {
            this.whereto = false;
            window.eventBus.$emit('nowhereto',false);
        })
    },
    watch: {
        whereto(val) {
            if (val === true) {
				this.showWhereto();  
            }
        }
    },
}
</script>

<style>
.form-group.label-floating label.control-label, .form-group.label-placeholder label.control-label {
    top: 18px;
    /* font-size: 21px !important; */
    line-height: 1.42857;
    left: 20px;
}

.form-group.label-static label.control-label, .form-group.label-floating.is-focused label.control-label, .form-group.label-floating:not(.is-empty) label.control-label, .form-group.has-bootstrap-select label.control-label {
    top: 10px;
    font-size: 11px !important;
    line-height: 1.07143;
}
.whereto {
	padding: 10px 30px;
}
.fix {
	float: left;
	width:40%;
	text-align: center;
	padding: 20px 0;
}
.fix img {
	margin: 0 auto !important;
}
.fix span {
	width: 100%;
	text-align: center;
}
.vs {
	width: 20%;
	padding: 30px 10px;
	float: left;
}
.viewMore {

}
.whatmatch a {
	color: inherit;
	width: 100%;
	transition: transform 1.9s ease-in-out;
}
.whatmatch a:hover {
	color:#08ddc1;
}
.whatmatch a:hover .fix img {
	transform: scale(1.3);
}
</style>