<template>
  <!-- Right Sidebar -->

  <div
    class="col col-xl-3 order-xl-3 col-lg-6 order-lg-3 col-md-6 col-sm-6 col-12 d-none d-sm-none d-md-block">
    <div class="ui-block">
      <div class="ui-block-title">
        <h6 class="title" style="width: 100%">Match Day Deals - Fan Kits</h6>
      </div>
    
      <div class="ui-block-content showMoreDeals">
        <a href="https://sheet2site.com/api/v3/index.php?key=1HueFJoqsyeLxEPZcO_SmC1DghNy3ZcrF_TnmLCQjLoQ&g=1" target="_blank">Click on your teams to go to their official store</a>  
      </div>
    </div>

    <div class="ui-block" style="max-height: 400px;overflow: auto;">
      <div class="ui-block-content">
        <iframe id="sofa-standings-embed-1-23776" width="100%" height="727px" style="height:727px!important" src="https://www.sofascore.com/tournament/1/23776/standings/tables/embed" frameborder="0" scrolling="no"></iframe>
      </div>
    </div>

    <div class="ui-block" data-spy="affix" data-offset-top="7">
      <div class="ui-block-title">
        <h6 class="title" style="width: 100%">Businesses on {{ projectName }}</h6>
      </div>
      <ul class="widget w-twitter" ref="dealstofollow">
        <li v-for="(businesses, index) in business" :key="index" class="twitter-item">
          <div class="author-folder">
            <img
              v-if="businesses.logo === '' || businesses.logo === null "
              src="/img/twitter-avatar1.png"
              alt="avatar"
            />
            <img v-else width="40" style="border-radius:4px" :src="businesses.logo" alt="avatar" />
            <div class="author">
              <router-link :to="'/profile/' + businesses.username" class="author-name">{{ businesses.business_name }}</router-link>
              <a href="#" class="group">@{{businesses.username}}</a>
              <span class="verified">
                <i class="fa fa-check" aria-hidden="true"></i>
              </span>
            </div>
          </div>
          <p>{{ $substring(businesses.business_desc, '100') }}</p>
          <a href="javascript:;" @click="() => businesses.is_following === true ? unFollowDeals(businesses.id,index) : followDeals(businesses.id,index)" :id="followBtn(index)" class="social-item bg-mdd">
            <!-- <i class="fab fa-twitter" aria-hidden="true"></i> -->
            {{ businesses.is_following === true ? 'Unfollow' : 'Follow' }}
          </a>
        </li>
      </ul>
      <div class="ui-block-content showMoreDeals">
        <router-link :to="{name: 'BusinessToFollow'}">Show more</router-link>
      </div>
    </div>
    
  </div>

  <!-- ... end Right Sidebar -->
</template>

<script>
  import ls from "../../localstorage";

var pageData = {
  projectName: "",
  userData: {},
  id: null,
  avatar: "",
  signedIn: false,
  business: {},
  errorData: {},
};
var loader;
export default {
  name: "RightSideBar",
  data() {
    return pageData;
  },
  methods: {
    followBtn(index) {
      return 'followBtn_' + index;
    },
    showLoading() {
      let loader = this.$loader(this, this.$refs.dealstofollow, {
        loaderType: "spinner"
      });
      return loader;
    },
    getBusinesses() {
      loader = this.showLoading();
      // this.checkFollowers();
      if (ls.checkStorage("fansData")) {
        let userData = ls.getStorage("fansData");
        this.axios
                .get(`businesses/recommended/${userData[0].id}`)
                .then(response => {
                  loader.hide();
                  this.business = response.data.data.businesses.data;
                  // console.log('deals to follow',response.data.data.businesses.data);
                })
                .catch(error => {
                  loader.hide();
                  // console.log(error.response);
                });
      }else {
        this.axios
                .get(`businesses/recommended/0`)
                .then(response => {
                  loader.hide();
                  this.business = response.data.data.businesses.data;
                  // console.log('deals to follow',response.data.data.businesses.data);
                })
                .catch(error => {
                  loader.hide();
                  // console.log(error.response);
                });
      }
    },
    followDeals(bid,index) {
      if(this.signedIn === true) {
        $("#followBtn_" + index).parent(".twitter-item").css('opacity','0.6');
        $("#followBtn_" + index).html('...');
        this.axios.post('user/follow/' + bid)
        .then(response => {
          // console.log(response);
          $("#followBtn_" + index).parent(".twitter-item").css('opacity','1');
          $("#followBtn_" + index).html('Unfollow');
          this.getBusinesses();
          this.errorData = {
            message: response.data.message,
            isError: "success"
          }
        })
        .catch(error => {
          // console.log(error.response);
          $("#followBtn_" + index).parent(".twitter-item").css('opacity','1');
          $("#followBtn_" + index).html('Follow');
          this.errorData = {
            message: error.response.data.message,
            isError: true
          }
        })
      }else {
        $('#loginBtn').click();
      }

    },
    unFollowDeals(bid,index) {
      if(this.signedIn === true) {
        $("#followBtn_" + index).parent(".twitter-item").css('opacity','0.6');
        $("#followBtn_" + index).html('...');
        this.axios.post('user/unfollow/' + bid)
        .then(response => {
          // console.log(response);
          $("#followBtn_" + index).parent(".twitter-item").css('opacity','1');
          $("#followBtn_" + index).html('Follow');
          this.getBusinesses();
          this.errorData = {
            message: response.data.message,
            isError: "success"
          }
        })
        .catch(error => {
          // console.log(error.response);
          $("#followBtn_" + index).parent(".twitter-item").css('opacity','1');
          $("#followBtn_" + index).html('Unfollow');
          this.errorData = {
            message: error.response.data.message,
            isError: true
          }
        })
      }else {
        $('#loginBtn').click();
      }

    },
    checkFollowers() {
      this.axios.get('/user/following')
      .then(response => {
        // console.log(response.data);

      })
      .catch(error => {
        // console.log(error.response);

      })
    },
    checkUserData(userData) {
      if (userData !== "" || userData !== "undefined") {
        this.userData = JSON.parse(userData);
        this.signedIn = true;
      }
    },
  },
  mounted() {
    this.projectName = this.$store.state.projectName;
    this.getBusinesses();
    var userData = localStorage.fansData;
    if (userData) {
      this.checkUserData(userData);
    }

    let standings = document.createElement('script')
    standings.setAttribute('src', 'https://www.sofascore.com/bundles/sofascoreweb/js/bin/util/embed.min.js')
    document.head.appendChild(standings)
    let standings2 = document.createElement('script')
    standings2.setAttribute('src', 'sofa_embed(\'sofa-standings-embed-1-23776\', window)')
    document.head.appendChild(standings2)

  }
};
</script>

<style>
  .showMoreDeals {
    border-top: solid 1px #eee;
  }
  .showMoreDeals a {
    color: #00AEEF;
  }
</style>
