<template>
  <div>
    <!-- Main Header Account -->

    <div class="main-header">
      <div
        class="content-bg-wrap bg-account"
        style="background-image:url('/image/epl_refree.jpg');"
      ></div>
      <div class="container">
        <div class="row">
          <div class="col col-lg-8 m-auto col-md-8 col-sm-12 col-12">
            <div class="main-header-content" style="margin-top: 78px;">
              <a
                data-toggle="modal"
                @click="() => { whereto = true }"
                data-target="#faqs-popup"
                href="#"
                class="btn btn-secondary btn-sm"
                style="border-radius: 70px;background-color: #fff;border: none;box-shadow: 0 4px 8px rgba(0,0,0,.2);"
              >
                <h4 style="color:#444;font-weight:bold">
                  What match ?
                  <svg
                    class="olymp-dropdown-arrow-icon"
                    style="fill:#38a9ff;position: relative;top:4px;width: 17px;"
                  >
                    <use xlink:href="/svg-icons/sprites/icons.svg#olymp-dropdown-arrow-icon" />
                  </svg>
                </h4>
              </a>
            </div>
          </div>
        </div>
      </div>
      <div class="img-bottom"></div>
    </div>

    <!-- ... end Main Header Account -->

    <FunctionsBar />

    <!-- ... end Top Header-Profile -->
    <div class="container" ref="bodyloady">
      <div class="row">
        <LeftSideInfo />
        <div class="col col-xl-6 order-xl-2 col-lg-12 order-lg-1 col-md-12 col-sm-12 col-12">
            <router-view />
        </div>
        <RightSideInfo />
      </div>
    </div>

    <Footer />


    <a class="back-to-top" style="background-color: #00AEEF" href="javascript:;">
      <img src="/svg-icons/back-to-top.svg" alt="arrow" class="back-icon" />
    </a>

    <Whereto :whereto="whereto" @goBack="() => { whereto = false; }" />
  </div>
</template>

<script>
import FunctionsBar from "../../components/Home/FunctionsBar";
import LeftSideInfo from "../../components/Home/LeftSideInfo";
import RightSideInfo from "../../components/Home/RightSideInfo";
import Footer from "../../components/Home/Footer";
import NewsFeed from "../../components/Home/NewsFeed";
import Whereto from "../../components/Modals/whereto";
import ls from "../../localstorage";

var loader;
var pageData = {
  projectName: "",
  whereto: false
};

export default {
  name: "Home",
  components: {
    FunctionsBar,
    LeftSideInfo,
    RightSideInfo,
    NewsFeed,
    Whereto,
    Footer
  },
  data() {
    return pageData;
  },
  methods: {
    showLoading() {
      let loader = this.$loader(this, this.$refs.loadContents);
      return loader;
    },
  },
  mounted() {
	// loader = this.showLoading();
    let $ = this.$Jquery;
    this.projectName = this.$store.state.projectName;

    window.eventBus.$on("nowhereto", id => {
      this.whereto = id;
    });

    $("#site-header").removeClass("switched");

    // this.getUser();
    $(".back-to-top").on("click", function() {
      $("html,body").animate(
        {
          scrollTop: 0
        },
        1200
      );
      return false;
    });
    var $window = $(window),
      $document = $(document),
      $body = $("body"),
      $sidebar = $(".fixed-sidebar");
    //$preloader = $('#hellopreloader');
    /* -----------------------------
     * Toggle functions
     * ---------------------------*/

    $('a[data-toggle="tab"]').on("shown.bs.tab", function(e) {
      var target = $(e.target).attr("href"); // activated tab
      if ("#events" === target) {
        $(".fc-state-active").click();
      }
    });

    // Toggle inline popups

    var $popup = $(".window-popup");

    $(".js-open-popup").on("click", function() {
      var target_popup = $(this).data("popup-target");
      var current_popup = $popup.filter(target_popup);
      var offset = $(this).offset();
      current_popup.addClass("open");
      current_popup.css("top", offset.top - current_popup.innerHeight() / 2);
      $body.addClass("overlay-enable");
      return false;
    });

    // Close on "Esc" click
    $window.keydown(function(eventObject) {
      if (eventObject.which == 27) {
        $popup.removeClass("open");
        $body.removeClass("overlay-enable");
        $(".profile-menu").removeClass("expanded-menu");
        $(".popup-chat-responsive").removeClass("open-chat");
        $(".profile-settings-responsive").removeClass("open");
        $(".header-menu").removeClass("open");
        $(".js-sidebar-open").removeClass("active");
      }
    });

    // Close on click outside elements.
    $document.on("click", function(event) {
      if (!$(event.target).closest($popup).length) {
        $popup.removeClass("open");
        $body.removeClass("overlay-enable");
        $(".profile-menu").removeClass("expanded-menu");
        $(".header-menu").removeClass("open");
        $(".profile-settings-responsive").removeClass("open");
      }
    });

    // Close active tab on second click.
    $("[data-toggle=tab]").on("click", function() {
      /*$body.toggleClass('body--fixed');*/
      if (
        $(this).hasClass("active") &&
        $(this)
          .closest("ul")
          .hasClass("mobile-app-tabs")
      ) {
        $($(this).attr("href")).toggleClass("active");
        $(this).removeClass("active");
        return false;
      }
    });

    // Close on "X" click
    $(".js-close-popup").on("click", function() {
      $(this)
        .closest($popup)
        .removeClass("open");
      $body.removeClass("overlay-enable");
      return false;
    });

    $(".profile-settings-open").on("click", function() {
      $(".profile-settings-responsive").toggleClass("open");
      return false;
    });

    $(".js-expanded-menu").on("click", function() {
      $(".header-menu").toggleClass("expanded-menu");
      return false;
    });

    $(".js-chat-open").on("click", function() {
      $(".popup-chat-responsive").toggleClass("open-chat");
      return false;
    });
    $(".js-chat-close").on("click", function() {
      $(".popup-chat-responsive").removeClass("open-chat");
      return false;
    });

    $(".js-open-responsive-menu").on("click", function() {
      $(".header-menu").toggleClass("open");
      return false;
    });

    $(".js-close-responsive-menu").on("click", function() {
      $(".header-menu").removeClass("open");
      return false;
    });
  }
};
</script>
